@import "../src/asset/Sass/main.scss";




.loading-wrapper {
    position: fixed;
    z-index: 9999;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0, 0.8);
}

.loader-container {
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    transform: translateY(-50%);
    display: block;
    width: 40%;
    border-radius: 6px;
    margin: auto;
    min-height: 80px;
}
.Under {
    z-index: 999;
    position: absolute;
    left: 0;
    right: 0;
    top: 0px;
    background: #2497FB;
    width: 242px;
    margin: auto;
    color: white;
    height: 33px;
    padding: 15px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}
.state-pending .copy_right{
    padding: 15px;
    bottom: 275px;
    left: 0;
    right: 0;
    width: 200px;
    margin: auto;
    font-size: 0.8em;
}
.state-pending {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    width: 100%;
    line-height: 0;
    text-align: center;
    background: white;
    padding: 5px;
    border-radius: 17px;
}


.state-pending img{border-radius: 15px;}

#under_duild {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 99999;
    overflow: hidden;
}
@media (max-width: 600px) {
    .loader-container {
        width: 90%;
    }
}